@charset "utf-8";
/* CSS Document */
.uploadify-button{
    position: absolute;
    top: 1rem;
    left: 0;
    width: 100%;
    height: 1.26rem;
    display:inline-block;
    line-height:24px;
    border-radius:12px;
    font-size:12px;
    font-weight: 600;
    font-family: '微软雅黑';
    color:#FFF;
    cursor:pointer;
    text-decoration:none;
}
.uploadify-button:hover{
}
.uploadfile{
    width:0;
    }
.uploadify-queue .uploadify-queue-item{
    list-style-type:none;
    margin-top:10px;
    }
.uploadbtn,.delfilebtn{
    display:inline-block;
    border:1px solid #999;
    line-height:24px;
    border-radius:4px;
    padding:0 18px;
    font-size:12px;
    color:#666;
    cursor:pointer;
    background:url(images/btnbg.png) repeat-x 0 0;
    text-decoration:none;
    }
.up_filename,.progressnum,.delfilebtn,.uploadbtn,.up_percent{
    font-size:12px;
    color:#666;
    margin-left:10px;
    }
.uploadify-progress{
     display:inline-block; 
     width:600px; 
     height:10px; 
     background-color:white;
     border-radius:20px;
     border:2px groove #666;
     vertical-align:middle;
     padding:0;
     }
.uploadify-progress-bar{
    width:0;
    height:100%;
    border-radius:20px;
    background-color: #0099FF;
    }
